.content {
  padding: 7%;
  font-family: "Unna", serif; }

.content_sm {
  background-color: white;
  border-radius: 20px;
  opacity: 0.9;
  padding: 3%; }

.menu {
  text-align: left;
  padding-top: 2%;
  padding-bottom: 2%; }

.menu_button {
  color: gray;
  margin-bottom: 3%;
  border-width: 3px;
  font-size: 150%; }

.title {
  font-size: 300%;
  text-align: left;
  font-weight: 900; }

.title_sm {
  font-size: 60%; }

.container_ {
  padding: 2%; }

.section {
  font-size: 120%;
  text-align: left; }

.small_title {
  font-style: italic;
  position: relative;
  z-index: 1;
  overflow: hidden;
  text-align: left; }
  .small_title:after, .small_title:before {
    position: absolute;
    top: 51%;
    overflow: hidden;
    width: 48%;
    height: 1px;
    content: "\a0";
    background-color: #cccccc;
    margin-left: 2%; }
  .small_title:before {
    margin-left: -50%;
    text-align: right; }

.Img_box {
  padding-top: 3%;
  padding-bottom: 3%; }

.video_box {
  padding-top: 3%;
  padding-bottom: 3%;
  width: 80%;
  position: relative;
  left: 10%; }

.expcontainer {
  text-align: center; }

.cp-text {
  font-size: 130%;
  color: white;
  text-align: center;
  padding-top: 10%; }

.example_box {
  padding-top: 3%;
  padding-bottom: 3%;
  width: 50%; }

.poster {
  padding-top: 3%;
  padding-bottom: 3%; }

.description {
  padding-top: 3%;
  padding-bottom: 3%;
  line-height: 1.8em;
  font-size: 110%;
  text-align: left; }

.news_content {
  padding: 0.5%; }

.image_box {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  width: 30%;
  height: 30%; }

.flex_box {
  margin: 2%;
  border-radius: 5px;
  text-align: center; }

.flex_box_job {
  font-size: 80%;
  font-weight: 400; }

.icon {
  font-size: 20px;
  color: lightgray;
  text-decoration: none;
  background-color: none;
  display: inline;
  padding: 2%;
  font-size: 120%; }

.example_img {
  padding-left: 10%;
  padding-right: 10%;
  padding-bottom: 10%; }

.img_name_r {
  font-size: 150%;
  font-weight: 800;
  text-align: center; }

.img_name_t {
  font-size: 150%;
  font-weight: 800;
  text-align: center;
  opacity: 0; }

